.poppins {
  font-family: 'Poppins', sans-serif;
}

* {
  font-family: 'Poppins', sans-serif;
}

::-webkit-calendar-picker-indicator {
  color: rgba(0, 0, 0, 0);
  /* background-image: url('./icons//calendar.svg'); */
  cursor: pointer;
  height: 20px;
}

::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}
::-webkit-scrollbar-thumb {
  background: #da3b19;
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: #ff3b59;
}
::-webkit-scrollbar-track {
  background: #ffffff;
  border-radius: 10px;
  box-shadow: inset 7px 10px 12px 0px #f0f0f0;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

